 @import "_nullStyle.css";

body {
  background: rgb(134, 26, 123);
  margin: 0;
  color: #fff;
}

.material-icons {
  font-size: 35px;
}

.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  animation: show 500ms ease-in-out;
}

@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.basket-quantity {
  z-index: 20;
  cursor: pointer;
  vertical-align: middle;
  color: #26A69A;
  font-size: 20px;
}

.basket-quantity:nth-child(1) {
  margin-left: 16px;
}

.basket-quantity:nth-child(2) {
  margin-right: 16px;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart {
  cursor: pointer;
}

.collection {
  z-index: 2;
  position: fixed;
  margin-top: 69px;
  order: 1;
  width: 100%;
  background-color: #250536d4;
  padding: 8px 24px;
}
.collection-title {
  font-size: 32px;
}

.collection-item {
  width: 100%;
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.collection-item span {
  justify-self: flex-end;
  margin-left: auto;
  cursor: pointer;
}
.collection-item i {
  font-size: 24px;
}
.collection-sum {
  margin-top: 16px;
  font-size: 20px;
}
.collection-sum img {
  height: 28px;
  width: auto;
  margin-left: 8px;
}

.toast-container {
  z-index: 20;
  background-color: #26A69A;
  color: #250536d4;
  width: 100%;
  text-align: center;
  position: fixed;
  top: 69px;
  animation: fade-in 250ms ease-in-out;
}

.header {
  z-index: 10;
  position: fixed;
  order: 0;
  width: 100%;
  height: 69px;
  padding: 10px 10vw;
  background-color: rgb(65, 14, 60);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart {
  display: flex;
  align-items: center;
}

.cart span {
  font-size: 28px;
  margin: -4px 0 0 8px;
}

.goods {
  z-index: 1;
  order: 2;
  width: auto;
  max-width: 1200px;
  min-height: calc(100vh - 70px - 64px);
  padding-top: 96px;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
  margin-top: 200px;
}
.goods.is_active {
  margin-top: 0;
}
.card {
  width: 420px;
  position: relative;
  cursor: pointer;
  z-index: 5;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 10px rgba(0,0,0,0.5); 
}
.card-image {
  width: 100%;
  height: auto;
  background-color: #4ddee1;
}
.card-content {
  background-color: #ffffff;
  color: black;
  padding: 3% 6%;
  font-size: 16px;
}
.card-action {
  background-color: #ffffff;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40px;
  padding: 3% 6%;
}
.card-action button {
  justify-self: flex-start;
  margin-right: auto;
  background-color: #26A69A;
  color: white;
  font-size: 18px;
  line-height: 100%;
  padding: 4px 16px 8px 16px;
}
.card-action p {
  font-size: 20px;
  padding: 4px 0 8px 0;
  color: black;
}

@media(max-width:500px) {
  .card {
    width: 315px;
  }
  .card-content {
    padding: 2.25% 4.5%;
  }

}

footer {
  background-color: #15171a;
  padding: 8px 10%;
  margin-top: 80px;
} 